@import '../../css/AllSites.css';
@import './question-wizard.css';

body, h1, h2, h3, h4, h5, h6 {
  font-family: "Helvetica Neue", Helvetica, "Segoe UI", Arial, freesans, sans-serif;
}
h1 {
  text-align: left;
  font-size: 2.8em;
  font-weight: 400;
  color: #222;
}
h2 {
  font-size: 1.8em;
  font-weight: 500;
}
h3 {
  font-size: 1.5em;
  font-weight: 500;
}
h4 {
  font-size: 1.2em;
  font-weight: 500;
}

.TableAsTree .wdk-CheckboxTreeItem__leaf .wdk-CheckboxTreeNodeContent span {
  background: yellow;
  font-weight: bold;
}

.form-group {
  margin: .6em 0;
}

.form-radio {
  margin: .4em;
}

#wdk-container {
  min-height: calc(100% - 206px);
  position: relative;
  text-rendering: optimizeLegibility;
}

/* 230px is based on height of header, footer, and margin/padding of wdk-PageContent */
.wdk-PageContent {
  background-color: #ffffff;
  min-height: calc(100vh - 230px);
}

.wdk-NotFound p {
  font-size: 1.2em;
}

.wdk-RecordOverview:after {
  content: "";
  display: table;
  clear: both;
}

/* FIXME: Remove when rest of page is responsive */
.wdk-RecordContainer {
  max-width: calc(100vw - 4em);
}

.wdk-RecordSidebarToggle,
.wdk-RecordNavigationSectionHeader {
  color: #777;
}

.wdk-UserDatasetOverview {
  font-size: 1.4em;
}

.wdk-UserDatasetOverviewItemText {
  font-weight: 300;
}

.wdk-UserDatasetSection {
  font-size: 1.2em;
  font-weight: 400;
}

.wdk-UserDatasetSectionHeading {
  font-size: 1.4em;
}

.wdk-DataTableCellExpand {
  color: #777;
}

td.wdk-DataTableCell__thumbnail {
  vertical-align: middle;
  height: 50px;
}

.wdk-RecordSection__collapsed {
  display: none;
}

/* offset so things don't hide under menubar */
.wdk-RecordSection,
.wdk-RecordSubsection {
  padding-top: 32px;
}

.wdk-RecordSection:after,
.wdk-RecordSubsection:after {
  margin-top: -42px;
  content: '';
  display: block;
}

.wdk-RecordTableContainer,
.wdk-RecordAttributeSectionItem {
  padding-top: 32px;
}

.wdk-RecordTableContainer:after,
.wdk-RecordAttributeSectionItem:after {
  margin-top: -32px;
  content: '';
  display: block;
}

.eupathdb-ReporterFormSubmit {
  text-align: center;
  width: 90vw;
}

@media all and (min-width: 800px) {
  .eupathdb-ReporterForm {
    display: flex;
    flex-wrap: wrap;
  }

  .eupathdb-ReporterFormGroup { width: 350px; }

  .eupathdb-ReporterForm__shared .eupathdb-ReporterFormGroup__columns      { order: 1; }
  .eupathdb-ReporterForm__shared .eupathdb-ReporterFormGroup__otherOptions { order: 2; }
  .eupathdb-ReporterForm__shared .eupathdb-ReporterFormGroup__tables       { order: 3; }
}

/* break point based on combined width of all ReporterFormGroup elemens */
@media all and (min-width: 1024px) {
  .eupathdb-ReporterFormGroup { margin: 0 auto 0 2em; }
  .eupathdb-ReporterFormGroup__otherOptions { width: auto; }
  .eupathdb-ReporterForm__shared .eupathdb-ReporterFormGroup__columns      { order: 1; }
  .eupathdb-ReporterForm__shared .eupathdb-ReporterFormGroup__tables       { order: 2; }
  .eupathdb-ReporterForm__shared .eupathdb-ReporterFormGroup__otherOptions { order: 3; }
}

.eupathdb-ReporterFormGroup .wdk-CheckboxTree-list li label {
  white-space: initial;
}

.eupathdb-ExpressionGraphsDataTableContainerHeader {
  font-weight: 500;
  margin: 0;
}

.eupathdb-ExpressionGraphsDataTableContainer td:nth-child(1),
.eupathdb-ExpressionGraphsDataTableContainer td:nth-child(2) {
  white-space: normal;
}

.eupathdb-DatasetGraphContainer {
  overflow: auto;
  position: relative;
  padding: 0 1rem;
  min-height: 100px;
  display: flex;
  flex-wrap: wrap;
}
.eupathdb-DatasetGraphContainer2 > h4 {
  position: absolute;
  right: 0;
  margin-right: 1em;
}
.eupathdb-DatasetGraphLoading {
  position: absolute;
  top: 1em;
  left: 1em;
}

.eupathdb-DatasetGraph {
  min-width: 500px;
  flex-grow: 1;
}

.eupathdb-DatasetGraphDetails {
  width: 50%;
  flex-grow: 1;
}

.eupathdb-DatasetGraphDescriptionHeader {
  margin: 0;
}

/** Dataset page */
.eupathdb-DatasetRecord {
  padding: 0 2em;
}

.eupathdb-DatasetRecord h2,
.eupathdb-DatasetRecord h3 {
  margin: 22px 0 11px;
  color: #333333;
  font-family: Arial, Helvetica, sans-serif;
}
.eupathdb-DatasetRecord-summary {
  font-size: 1.2em;
}
.eupathdb-DatasetRecord ul {
  line-height: 1.6;
  list-style: none;
  padding-left: 1em;
  margin: 0;
}
.eupathdb-DatasetRecord-title {
  color: black;
}
.eupathdb-DatasetRecord-headerTable tr th {
  white-space: nowrap;
  padding-right: 1em;
  vertical-align: top;
  text-align: right;
  border: none;
}

.eupathdb-DatasetRecord-GraphImg {
  width: 450px;
}

